.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(#032fdd, #09ae38);
  min-width: 750px;
  min-height: 860px;
}

.title {
  text-align: center;
  margin: 10px 30px;
  font-size: 45px;
  color: white;
  animation: girarTitulo 1s;
}

.containerInput {
  border: 1.5px solid white;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 15px;
  margin: 34px 0;
  display: flex;
  justify-content: space-between;
  border-radius: 25px;
  box-shadow: 1px 3px 8px rgba(0, 0, 0, 0.5);
  width: 60%;
}

.containerInput input {
  width: 100%;
  background-color: transparent;
  border: 0;
  font-size: 25px;
  color: white;
  padding: 10px;
}

.containerInput input:focus {
  outline: 0;
}
